.common_style > .subject {
    text-align: center;
    margin: 10px 0px 10px;
    background: #c4c4c4;
    border-radius: 10px;
}
.common_style  .mtitle {
    margin: 5px 0px 5px;
    font-size: 25px;
    color: blue;
    background: white;
    border-radius: 10px;
    padding: 0px 5px;
    box-sizing: border-box;
    display: block;
}
.common_style  .stitle {
    margin: 5px 0px 5px;
    font-size: 20px;
    font-weight: 800;
    color:violet;
    background: #eeeeee;
    border-radius: 2px;
    padding: 5px 5px;
    box-sizing: border-box;
    display: block;
    border-radius: 10px;
}
.common_style  .lblock {
    border: black solid 3px;
    background: rgba(0,255,0,0.3);
    margin: 10px 0px 20px;
    width: 100%;
    min-height: 150px;
    padding: 5px;
    box-sizing: border-box;
    display: block;
    border-radius: 10px;
    font-size: 1em;
}
.common_style  .mblock {
    border: black solid 3px;
    background: rgba(0,0,255,0.3);
    margin: 10px 0px 10px;
    width: 100%;
    min-height: 80px;
    padding: 5px;
    box-sizing: border-box;
    display: block;
    border-radius: 10px;
}
.common_style  .sblock {
    border: black solid 3px;
    background: rgba(255,0,0,0.3);
    margin: 10px 0px 10px;
    width: 100%;
    min-height: 80px;
    padding: 5px;
    box-sizing: border-box;
    display: block;
    border-radius: 10px;
}
/* 일반 글자 */
.common_style p {
    background-color: rgba(230,230,230,0.1);
    border-bottom: white dotted 1px;
}
.common_style .hyperlink>a {
    margin-left: 10px;
    text-decoration: none;
    white-space: wrap;
    
}
.common_style .hyperlink>a:hover {
    background: rgba(0,255,0);
    border-radius: 5px;
}
/* 이미지위에 마우스 올라가면 화면 확대 */
.common_style img {
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
}
.common_style img:hover {
    transform: scale(1.2);
    transition: all 0.3s ease-in-out;
}

/* 단어 : 설명 을 구분하기 위해 단어에 색을 준 스타일 */
.common_style i {
    color: red;
    font-weight: 800;
}
/* 주석 용도로 사용 */
.common_style small {
    color: green;
    font-weight: 800;
}